/* RESET STYLES */

@import-normalize;

* {
  margin: 0;
  padding: 0;
  outline: 0;
  font-size: 100%;
  vertical-align: baseline;
}

/*-------------- ALL PAGES --------------*/
html,
body {
  height: 100%;
  font-family: "EB Garamond", serif;
}

body {
  background-color: #fff9f6;
  overflow-x: hidden;
}

.dark-mode {
  background-color: #071013;
  color: #e0e0e0;
}

main {
  margin: 150px 300px 100px 300px;
  min-height: 100vh;
}

h1 {
  text-align: center;
  margin-bottom: 20px;
  font-size: 20px;
}

a {
  color: inherit;
  text-decoration: none;
}

li {
  list-style-type: none;
}

/* HEADER/NAV */

nav {
  color: black;
  background-image: url("./images/torn-paper-pale.png");
  background-size: 105%;
  background-position: bottom;
  height: 120px;
  position: fixed;
  top: 0;
  z-index: 100;
  width: 100%;
}

.navbar {
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  justify-items: center;
}

.home {
  position: absolute;
  left: -20px;
  top: -20px;
  transform: rotate(25deg);
}

.home img {
  width: 150px;
}

.nav-main {
  grid-column-start: 2;
  display: flex;
  gap: 50px;
  margin-top: 10px;
  font-family: "Roboto Condensed", sans-serif;
  color: #241913;
  text-transform: uppercase;
  font-weight: 700;
  font-size: 15px;
}

.nav-main-item {
  display: flex;
  gap: 7px;
  flex-direction: column;
  align-items: center;
}

.image-bag {
  background-size: 60px;
  width: 60px;
  height: 47px;
  display: flex;
  justify-content: center;
  align-items: center;
}

.nav-img-one {
  background-image: url("./images/splash-red.png");
}

.nav-img-two {
  background-image: url("./images/splash-green.png");
}

.nav-img-three {
  background-image: url("./images/splash-purple.png");
}

.nav-img-four {
  background-image: url("./images/splash-blue.png");
}

.nav-img-five {
  background-image: url("./images/splash-purple-pure.png");
}

.nav-main-icon {
  max-width: 40px;
  max-height: 40px;
}

.right-side {
  margin-top: 10px;
  margin-left: 200px;
  display: flex;
  gap: 15px;
}

.lantern-set {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 3px;
}

.lantern {
  width: 30px;
}

.lantern-set span {
  font-size: 10px;
  font-family: "Roboto Condensed", sans-serif;
  font-weight: bold;
}

.lantern-set:hover {
  cursor: pointer;
}

.logged-in {
  width: 131px;
  font-family: "Roboto Condensed", sans-serif;
  text-transform: uppercase;
  font-size: 10px;
  font-weight: bold;
  display: flex;
  flex-direction: column;
  justify-content: flex-end;
  align-items: center;
}

.navatar {
  width: 60px;
  height: 60px;
  box-sizing: border-box;
  border: 4px solid transparent;
  object-fit: cover;
  border-radius: 50%;
}

.navatar:hover {
  border: 4px solid #fff9f688;
}

.logout:hover {
  cursor: pointer;
  color: #95b424;
}

.login {
  /* position: relative;
  top: 10px;
  left: 370px;
  width: 50px;
  height: 30px; */
  /* padding: 17px 10px;
  border-radius: 50%; */
  /* border: 1px solid black; */
  display: flex;
  align-items: center;
  font-family: "Roboto Condensed", sans-serif;
  font-size: 0.7rem;
  font-weight: bold;
  text-transform: uppercase;
  text-align: center;
}

.login-group {
  display: flex;
  flex-direction: column;
}

.login-set {
  padding: 2px 0;
  display: flex;
  gap: 2px;
  justify-content: space-between;
}

.login input {
  width: 70px;
}

.login button {
  font-family: "Roboto Condensed", sans-serif;
  text-transform: uppercase;
  font-weight: bold;
  align-self: center;
  background-color: #fff9f6;
  border-radius: 5px;
  padding: 1px;
  margin-bottom: 1px;
  border: 2px solid black;
}

.login button:hover {
  cursor: pointer;
  background-color: #95b424;
  color: #fff9f6;
  border: 2px solid #fff9f6;
}

.signup-link {
  color: #ac15b9;
}

.signup-link:hover {
  text-decoration: underline;
}

/* FOOTER */
footer {
  display: flex;
  justify-content: center;
  align-items: flex-end;
  background-image: url("./images/mashed-colors.png");
  background-size: 110%;
  background-position: top;
  height: 150px;
  z-index: 1;
}

.footer-links {
  display: flex;
  justify-content: space-around;
  align-items: flex-end;
  font-family: "Roboto Condensed", sans-serif;
  text-transform: uppercase;
  font-size: 20px;
  font-weight: 700;
  color: rgb(72, 72, 72);
  padding-bottom: 20px;
  background-image: url("./images/torn-paper-tan.png");
  background-size: 150%;
  width: 1700px;
  height: 100px;
  background-position: top;
}

/* UPDATES */

.updates-page {
  display: flex;
  flex-direction: column;
}

.first {
  margin-bottom: 150px;
}

.barn-owl {
  height: 470px;
  position: relative;
  right: 50px;
  bottom: 80px;
}

.branch {
  height: 490px;
  position: relative;
  bottom: 120px;
}

.sunflower {
  height: 480px;
  width: auto;
  justify-self: flex-start;
  position: relative;
  top: 200px;
  right: 200px;
  z-index: -1;
}

.image-pair {
  display: flex;
  align-items: center;
  margin-top: 0;
}

.updates-text {
  display: flex;
  flex-direction: column;
  gap: 30px;
  min-width: 700px;
  max-width: 700px;
  max-height: 700px;
  min-height: 400px;
  padding: 20px;
  font-size: 20px;
}

.updates-text-light {
  background-color: #fefefe;
  border: 5px solid #f4efec;
  color: black;
}

.updates-text-dark {
  color: #e0e0e0;
  border: 5px solid #645116;
}

.two {
  margin-bottom: 140px;
}

.updates-text h2 {
  align-self: center;
  font-size: 25px;
}

.updates-text img {
  width: 75px;
  height: 75px;
  border-radius: 50%;
  border: 2px solid darkgrey;
  box-sizing: border-box;
}

.updates-text ul {
  display: flex;
  flex-direction: column;
  gap: 20px;
}

.updates-text li {
  display: flex;
  align-items: center;
  gap: 40px;
  border-bottom: 1px solid rgb(232, 230, 230);
  padding-bottom: 20px;
}

.li-header {
  display: flex;
  gap: 20px;
  width: 350px;
}

.li-header h4,
.standalone-li-header h4 {
  line-height: 3ch;
  font-weight: 400;
  font-size: 20px;
}

.header-date {
  display: flex;
  flex-direction: column;
}

.header-date span {
  font-size: 15px;
}

.standalone-li-header {
  display: flex;
  gap: 20px;
}

.text-body {
  width: 400px;
  display: -webkit-box;
  -webkit-box-orient: vertical;
  -webkit-line-clamp: 4;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: normal;
  line-height: 3.2ch;
  margin-right: 20px;
}

.more {
  text-align: right;
  margin-right: 25px;
}

.three {
  position: relative;
  right: 130px;
}

/* TASKS */
.task-page {
  display: grid;
  grid-template-columns: 1fr 3fr 1fr;
  margin: 150px 0 100px 0;
}

/* .task-page-dark {
  background-color: 11ee1e;
  border: 5px solid #614e15;
  color: #e0e0e0;
} */

.filter-options {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 20px;
}

.filter-box {
  padding: 15px 10px;
  width: 220px;
}

.filter-box-light {
  background-color: white;
  color: black;
  border: 3px solid #ebebeb;
}

.filter-box-dark {
  background-color: #06181c;
  color: #e0e0e0;
  border: 3px solid #645116;
}

.filter-groups {
  margin-top: 67px;
}

.filter-box h3 {
  margin-bottom: 15px;
  text-align: center;
  color: grey;
}

.filter-list {
  display: flex;
  flex-direction: column;
  gap: 10px;
}

.filter-li {
  display: flex;
  list-style-type: space-counter;
}

.filter-li span:hover {
  cursor: pointer;
}

.filter-li-group {
  display: flex;
  align-items: center;
}

.filter-color {
  width: 20px;
  height: 20px;
  box-sizing: border-box;
  border-radius: 50%;
  margin-right: 5px;
}

.status-filter-color {
  width: 5px;
  height: 5px;
  background-color: black;
  border: 3px solid black;
}

.filter-list div:hover {
  cursor: pointer;
}

.filter-scraps {
  display: flex;
  gap: 20px;
  flex-wrap: wrap;
}

.filter-scrap-light {
  background-color: white;
  color: black;
}

.filter-scrap-dark {
  background-color: #06181c;
  color: #c9a330;
}

.filter-scrap::after {
  content: " x";
  font-weight: bold;
}

.filter-scrap:hover {
  cursor: pointer;
}

.filter-li input {
  background-color: #241913;
}

.flex-between {
  display: flex;
  justify-content: space-between;
}

.tx-search {
  margin-bottom: 5px;
  text-align: center;
  display: flex;
}

.tx-search input {
  height: 27px;
  border: 1px solid #a1a1a1;
  border-radius: 3px;
  font-size: 15px;
  font-family: "EB Garamond", serif;
  padding-left: 5px;
  padding-right: 2px;
}

.search-button {
  background-color: white;
  margin-left: 10px;
  width: 70px;
  height: 27px;
  box-sizing: border-box;
  border: 1px solid #a1a1a1;
  font-size: 15px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  border-radius: 3px;
}

.search-button:hover {
  background-color: #a1a1a1;
  color: white;
  font-weight: bold;
  cursor: pointer;
}

.tx-head-col div:hover {
  cursor: pointer;
}

.tx-head-col span {
  color: darkgrey;
}

.tx-list {
  font-size: 17px;
  color: rgb(32, 32, 32);
  border-spacing: 1px 3px;
}

.tx-list td {
  padding: 7px 10px 7px 8px;
}

.tr-light {
  background-color: #ffffff;
}

.tr-dark {
  background-color: #06181c;
  color: #e0e0e0;
}

.tr-light:hover td {
  background-color: rgb(252, 252, 252);
}

.tr-dark:hover td {
  background-color: #193e49;
}

.tx-name-cell {
  text-align: left;
}

.tx-name-cell:hover {
  font-weight: bold;
}

.tx-group {
  text-align: left;
}

.tx-data {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding-top: 9px;
}

.tx-data-left {
  display: flex;
  align-items: center;
  gap: 5px;
}

.group-color {
  width: 8px;
  height: 8px;
  transform: rotate(45deg);
  background-color: #7b165e;
  margin-right: 10px;
}

.tx-status {
  color: grey;
}

.tx-navlinks {
  display: flex;
  justify-content: center;
}

.tx-footer {
  display: flex;
  gap: 10px;
}

.tx-footer-button {
  margin-top: 20px;
  width: 20px;
  border-radius: 5px;
  font-family: "Roboto Condensed", sans-serif;
}

.tx-footer-button-light {
  background-color: white;
  color: rgb(69, 69, 69);
  border: 1px solid grey;
}

.tx-footer-button-dark {
  background-color: #06181c;
  color: white;
  border: 1px solid #645116;
}

.tx-footer-button:hover {
  cursor: pointer;
  background-color: rgb(69, 69, 69);
  color: white;
}

.tx-qualified {
  display: flex;
  justify-content: space-between;
  margin-bottom: 10px;
}

td {
  text-align: center;
  vertical-align: middle;
}

.tx-desc {
  text-align: left;
  font-size: 15px;
}

.add-me {
  margin-left: 10px;
  color: #5c2e7e;
}

.tx-expand {
  margin-left: 10px;
  font-size: 15px;
}

.tx-expand:hover {
  cursor: pointer;
}

.hidden {
  display: none;
}

.lookout-below {
  margin-bottom: 10px;
}

.tx-view-mine {
  color: #5c2e7e;
}

.tx-view-mine:hover {
  font-weight: bold;
}

.space-below {
  margin-bottom: 30px;
}

th {
  padding-bottom: 15px;
}
